export function waterMask(options) {
  const {
    container = document.body, // 
    width = '300', // canvas
    height = '200', // canvas
    textAlign = 'left', // 
    textBaseline = 'bottom', // 
    font = '16px Microsoft Yahei', // 
    fillStyle = '#000', // 
    content = 'hellojackhui', // 
    globalAlpha = 0.1, // 
    rotate = 16, // 
    zIndex = 1000, // 
  } = options;
  let canvas = document.createElement('canvas');
  canvas.setAttribute('width', width);
  canvas.setAttribute('height', height);
  let ctx = canvas.getContext('2d');
  ctx.globalAlpha = globalAlpha;
  ctx.textAlign = textAlign;
  ctx.textBaseline = textBaseline;
  ctx.font = font;
  ctx.fillStyle = fillStyle;
  ctx.rotate((Math.PI * rotate) / 180);
  ctx.fillText(content, 50, 50);

  const base64Url = canvas.toDataURL();

  const __wm = document.querySelector('.__wm');//
  const waterMaskDiv = __wm || document.createElement("div");
  const styleStr = `
    position:absolute;
    top:0px;
    left:0px;
    width:100%;
    height:100%;
    z-index:${zIndex};
    pointer-events:none;
    background-repeat:repeat;
    background-image:url('${base64Url}')`;
  waterMaskDiv.setAttribute('style', styleStr);
  waterMaskDiv.classList.add('__wm');
  console.log(container)
  container.style.position = 'relative';
  if (!__wm) {
    container.appendChild(waterMaskDiv); // 
  }
  const MutationObserver = window.MutationObserver || window.WebKitMutationObserver;
  // API
  if (MutationObserver) {
    const args = arguments[0];
    let mo = new MutationObserver(function () {
      const __wm = document.querySelector('.__wm');
      // __wm
      if ((__wm && __wm.getAttribute('style') !== styleStr) || !__wm || container.style.position !== 'relative') {
        // 
        mo.disconnect();
        mo = null;
        waterMask(args);
      }
    });
    mo.observe(container, {
      attributes: true, // 
      subtree: true, // 
      childList: true, // 
    })
  }
}